

cc.Class({
    extends: cc.Component,

    properties: {
        information : cc.Node,
        image : cc.Node,
        select :  cc.Node
    },

    init : function( data , isTopCard ){
        if( !isTopCard ) this.active = false;

        //现在只会这么写
        //修改information区
        this.information.getChildByName("from").getComponent(cc.Label).stirng = data.from;
        this.information.getChildByName("name").getComponent(cc.Label).stirng = data.name;
        this.information.getChildByName("date").getComponent(cc.Label).stirng = data.date;
        this.information.getChildByName("information").getComponent(cc.Label).stirng = data.information;
        
        //修改img区域
        this.image.getComponent(cc.Sprite).spriteFrame = picUrl;

        //修改Select区域
        this.select.getChildByName("acceptButton").getComponent(cc.Label).stirng = "Accept";
        this.select.getChildByName("declineButton").getComponent(cc.Label).stirng = "Decline";
    }

});
